import React, { useState } from 'react';
import styles from '../../assets/modules/form.module.scss';

export default function Form({ handleSubmitForm, cardInputRef }) {
  const [inputValue, setInputValue] = useState('');
  const buttonDisabled = inputValue.length < 4;

  return (
    <form
      className={styles.form}
      onSubmit={(e) => {
		    handleSubmitForm(e, inputValue);
		    setInputValue('');
		  }}
    >
      <input
        ref={cardInputRef}
        className={styles.input}
        type="text"
        minLength={4}
        onChange={(e) => setInputValue(e.target.value)}
        value={inputValue}
        placeholder="–î–æ–±–∞–≤–∏—Ç—å –∑–∞–ø–∏—Å—å (–º–∏–Ω–∏–º—É–º 4 c–∏–º–≤–æ–ª–∞)"
      />
      <button type="submit" id="submit-btn" name="submit-btn" className={`btn btn-light ${styles.submitButton}`} disabled={buttonDisabled}>–î–æ–±–∞–≤–∏—Ç—å</button>
    </form>
  );
}
